/** @format */

@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
	font-family: "GTWalshiem";
	src: url("./assets/fonts/GTWalsheimPro-Medium.ttf") format("truetype");
	font-weight: 500;
}

body {
	cursor: none !important;
}

a:hover {
	cursor: none !important;
}

#cursor-regular {
	width: 8px;
	height: 8px;
}

#cursor-follower {
	width: 24px;
	height: 24px;
}

#cursor-regular,
#cursor-follower {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: white;
	mix-blend-mode: difference;
	border-radius: 50%;
	pointer-events: none;
	transition: transform 0.3s ease;
	z-index: 9999;
}

.follower-scale {
	transform: translate(-50%, -50%) scale(2) !important;
}

ul.custom-list {
	list-style: none;
}

ul.custom-list li::before {
	content: "-";
	margin-right: 5px;
}

.fade-in {
	-webkit-animation: fade-in 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) both;
	animation: fade-in 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}

.fade-out {
	-webkit-animation: fade-out 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) both;
	animation: fade-out 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}

/**
 * ----------------------------------------
 * animation fade-in
 * ----------------------------------------
 */
@-webkit-keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

/**
 * ----------------------------------------
 * animation fade-out
 * ----------------------------------------
 */
@-webkit-keyframes fade-out {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}
@keyframes fade-out {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}
